<template>
  <Layout>
    <PageHeader :title="title" :items="items" />

    <div class="row">
      <div class="col-12">
        <div class="card">
          <div class="card-body">
            <h4 class="card-title"></h4>
            <div class="row mt-4">
              <div class="col-sm-12 col-md-6">
                <div id="tickets-table_length" class="dataTables_length">
                  <label class="d-inline-flex align-items-center">
                    Show&nbsp;
                    <b-form-select v-model="perPage" size="sm" :options="pageOptions"></b-form-select>&nbsp;entries
                  </label>
                </div>
              </div>
              <!-- Search -->
              <div class="col-sm-12 col-md-6">
                <div id="tickets-table_filter" class="dataTables_filter text-md-right">
                  <label class="d-inline-flex align-items-center">
                    Search:
                    <b-form-input
                      v-model="filter"
                      type="search"
                      placeholder="Search..."
                      class="form-control form-control-sm ml-2"
                    ></b-form-input>
                  </label>
                </div>
              </div>
              <!-- End search -->
            </div>
            <!-- Table -->
            <div class="table-responsive mb-0">
              <b-table
                :items="sacs"
                :fields="fields"
                responsive="sm"
                :per-page="perPage"
                :current-page="currentPage"
                :sort-by.sync="sortBy"
                :sort-desc.sync="sortDesc"
                :filter="filter"
                :filter-included-fields="filterOn"
                @filtered="onFiltered"
              >
              <template v-slot:cell(Consecutivo)="data">
                 ATH-{{data.item.id_programacion}}
              </template>
             <template v-slot:cell(total)="data">
                 {{parseFloat(data.item.total)|moneda}}
              </template>
                <template v-slot:cell(actions)="data">

                <b-dropdown size="sm" class="">
                  <template v-slot:button-content>
                    Action
                    <i class="mdi mdi-chevron-down"></i>
                  </template>
                    <b-dropdown-item-button @click="editMode=true;ver=false;setear(data.item.id)"> Editar </b-dropdown-item-button>
                    <b-dropdown-item-button @click="eliminarCajero(data.item.id)"> Eliminar </b-dropdown-item-button>
                    <b-dropdown-item-button @click="editMode=false;ver=true;setear(data.item.id)"> Ver </b-dropdown-item-button>
                    <b-dropdown-item-button @click="editMode=false;ver=true;detalles(data.item)"> Detalles </b-dropdown-item-button>
                </b-dropdown>
                </template>
              </b-table>
            </div>
            <div class="row">
              <div class="col">
                <div class="dataTables_paginate paging_simple_numbers float-right">
                  <ul class="pagination pagination-rounded mb-0">
                    <!-- pagination -->
                    <b-pagination v-model="currentPage" :total-rows="rows" :per-page="perPage"></b-pagination>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>


    <b-modal id="modal" false size="lg"  title="Gestión de servicios a cobro ATH" hide-footer> 
              <p style="font-size:23px">Consecutivo:<b class="mx-auto"> ATH-{{form.id}}</b> </p> 
                    <table class="table my-3" >
                        <thead>
                          <tr>
                            <th scope="col">ITEM</th>
                            <th scope="col">PRODUCTO </th>
                            <th scope="col">VALOR UNITARIO</th>
                            <th scope="col">CANTIDAD</th>
                            <th scope="col">TOTAL</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr v-for="item in form.items" :key="item.id">
                            <th scope="row">{{item.id}}</th>
                            <td>{{item.descripcion}}</td>
                            <td>{{item.precio | moneda}}</td>
                            <td>{{item.cantidad}}</td>
                            <td>{{item.total | moneda}}</td>
                          </tr>
                        </tbody>
                      </table>
                      <p style="font-size:23px">Total:<b class="mx-auto">{{form.total | moneda}}</b> </p>

        <button class="btn btn-block float-right btn-success" @click="switchLoc" v-if="!ver && !editMode">Guardar</button>
        <button class="btn btn-block float-right btn-success" @click="switchLoc" v-if="!ver && editMode">Editar</button>
     </b-modal>
        

  </Layout>
</template>

<script>
import "vue-select/dist/vue-select.css";
import vSelect from "vue-select";
import Multiselect from "vue-multiselect";
import {mapState,mapMutations, mapActions} from 'vuex'
import { ValidationProvider, ValidationObserver } from "vee-validate";
import Layout from "../../layouts/main";
import PageHeader from "@/components/page-header";
import { tipologia } from "./tipologia";

/**
 * Dashboard component
 */
export default {
  components: {
    Layout,
    PageHeader,
    ValidationProvider,
    ValidationObserver,
    vSelect,
    Multiselect
  },
  data() {
    return {
      tipologia:tipologia,
      title: "Procesos",
      items: [
        {
          text: "Servicios a coboro"
        },
        {
          text: "ATH",
          active: true
        }
      ],
     dropzoneOptions: {
        thumbnailWidth: 150,
        maxFilesize: 0.5,
        headers: { "My-Awesome-Header": "header value" }
      },
      ver:false,
      url:"",
      url_firma:"",
      modal: true,
      file:null,
      firma:null,
      email: "",
      password: "",
      totalRows: 1,
      currentPage: 1,
      perPage: 10,
      pageOptions: [10, 25, 50, 100],
      filter: null,
      filterOn: [],
      entidades: [],
      sortBy: "age",
      sortDesc: false,
      fields: ["Consecutivo","total","status","actions"],
      sacs: [], 
      ciudades: [], 
      regional: [], 
      seguridad: [], 
      editMode:false,
      form:{
          'id':'',
          'nombre_tercero':'',
          'descripcion_tercero':'',
          'ciudades_id':'',
          'cierre_nocturno':'',
          'mantenimiento':'',
          'compartido_con':null,
      }
    }
  },
  computed:{
        ...mapState(['counter'])
   },
  filters: {
        moneda: function (value) {
          const formatterPeso = new Intl.NumberFormat('es-CO', {
             style: 'currency',
             currency: 'COP',
             minimumFractionDigits: 1
           })
           let valueFinal = formatterPeso.format(value);
           return valueFinal
        }
    },
  methods: {
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    }, 
    ...mapActions(['guardarUsuario']),
     switchLoc(){
      if (!this.editMode) {
        this.$refs.form.validate().then(esValido => {
            if (esValido) {
              this.agregarCajero();
            } else {}
          });        
        }else{
          this.$refs.form.validate().then(esValido => {
          if (esValido) {
            this.editarsacs();
          } else {
        }});
      }
    },
    detalles(data){
      console.log(data);
      this.$router.push({ name: 'cajero_details', params: { id: data.id,entidad: data.id_entidad }})
    },
   async agregarCajero(){
     let data = new FormData();
      var formulario = this.form;
        for (var key in formulario) {
          data.append(key,formulario[key]);
        }
       await this.axios.post('api/sacs/ath', data, {
           headers: {
            'Content-Type': 'multipart/form-data'
           }}).then(response => {
            if (response.status==200) {
               this.$swal(
                   'Agregado exito!',
                    '',
                    'success');
               this.listarSac();
               this.$root.$emit("bv::hide::modal", "modal", "#btnShow");
               ///limpiar el formulario
                for (var key in formulario) {
                   this.form[key]="";
                 }
              }
            }).catch(e => {
              console.log(e.response.data.menssage);
              this.$swal(e.response.data);
          });
      },
    async editarsacs(){
     let data = new FormData();
       var formulario = this.form;
        for (var key in formulario) {
          data.append(key,formulario[key]);
        }
        await this.axios.put('api/sacs/ath', data).then(response => {
            if (response.status===200) {
               this.$swal('Editado con exito','','success');
               this.listarSac();
               this.$root.$emit("bv::hide::modal", "modal", "#btnShow");
               ///limpiar el formulario
                for (var key in formulario) {
                   this.form[key]="";
                 }
              }
            }).catch(e => {
              console.log(e);
              this.$swal('No se pudo editar','','warning');
            });
     },
     async eliminarsacs(id){
        let data = new FormData();
        data.append('id',id);
        await this.axios.post('api/sacs/ath/delete',data, {
            headers: {
              'Content-Type': 'multipart/form-data'
            }}).then(response => {
              if (response.status==200) {
                this.$swal(
                    'Eliminado con exito!',
                      '',
                      'success'
                );
                this.listarSac();
                }
              }).catch(e => {
                console.log(e.response.data.menssage);
                this.$swal(e.response.data);
          });
      }, 
      eliminarCajero(id){
        this.$swal({
          title: 'Desea borrar este cajero?',
          icon: 'question',
          iconHtml: '',
          confirmButtonText: 'Si',
          cancelButtonText: 'No',
          showCancelButton: true,
          showCloseButton: true
        }).then((result) => {
          if (result.isConfirmed) {
            this.eliminarsacs(id);
          }
        })
      },
      resete(){
        var formulario = this.form;
        for (var key in formulario) {
             this.form[key]="";
       }
      },
      setear(id) {
        for (let index = 0; index < this.sacs.length; index++) {
          if (this.sacs[index].id===id) {
            this.form.id=this.sacs[index].id;
            this.form.consecutivo=this.sacs[index].id_programacion;
            this.form.total=this.sacs[index].total;
            this.form.items=JSON.parse(this.sacs[index].items);
            this.form.items=JSON.parse(this.form.items);
            this.$root.$emit("bv::show::modal", "modal", "#btnShow");
            return;
          }
        }
      },
     async listarSac(){
       await this.axios.get('api/sac/ath')
        .then((response) => {
          this.sacs = response.data.rows;
        })
        .catch((e)=>{
          console.log('error' + e);
        })
      },
      setEmail(){
        this.form.username=this.form.email;
        console.log("holas");
      },
      onFileChange(e) {
        const file = e.target.files[0];
        this.url = URL.createObjectURL(file);
      },
      toggleModal () {
        this.modal = !this.modal
      },
      session(){
        if (localStorage.getItem('token')) {
          const token=localStorage.getItem('token');
          this.guardarUsuario(token);
          }else{
          this.$router.push({ name: 'Home' });
          }
        }
  },
      created(){
        this.session();
        this.listarSac();
        this.listarEntidades();
        this.listarregional();
        this.listarseguridad();
      },
    computed: {
    rows() {
       return this.sacs.length
    },
  },
}
</script>
